
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html, body, #app {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  min-width: 980px;
  overflow: hidden;
}
#app {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
#app > header {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
    height: 60px;
}
#app > header .row > div {
      display: inline-block;
      padding: 0px 15px;
      height: 60px;
      line-height: 60px;
}
#app > header .row .logo, #app > header .row .intro {
      font-size: 2rem;
}
#app > header .row .app-menu {
      margin: 0;
}
#app > header .row .app-menu li {
        padding: 0 10px;
        float: left;
}
#app > header .row .app-menu li a {
          color: white;
          text-decoration: none;
}
#app > article {
    -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
            flex: 1 1 auto;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    display: -webkit-flex;
    overflow: hidden;
}
#app > article .nav-menu {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 auto;
              flex: 0 0 auto;
}
#app > article .nav-menu .lv1-menu {
        height: 100%;
        width: 50px;
        float: left;
}
#app > article .nav-menu .lv1-menu li {
          color: #fff;
          line-height: 3rem;
          cursor: pointer;
          text-align: center;
}
#app > article .nav-menu .lv1-menu li.active {
            background: #edeff0;
            color: #16499a;
            cursor: pointer;
}
#app > article .nav-menu .assistant {
        width: 160px;
        float: left;
        height: 100%;
        background: #edeff0;
}
#app > article .nav-menu .assistant h3 {
          font-weight: 400;
          line-height: 50px;
}
#app > article .nav-menu .assistant .lv2-menu li {
          padding: 0px 10px;
          font-size: 0.875rem;
          line-height: 40px;
          cursor: pointer;
}
#app > article .nav-menu .assistant .lv2-menu li a {
            display: block;
            color: #333;
            border-bottom: 1px solid #e5e5e5;
            text-decoration: none;
            font-size: 0.875rem;
}
#app > article .nav-menu .assistant .lv2-menu li a i {
              margin-right: 6px;
}
#app > article .nav-menu .assistant .lv2-menu li.active {
            background: #fff;
            border-bottom: 1px solid #888;
}
#app > article .nav-menu .assistant .lv2-menu li.active a {
              color: #16499a;
              border-bottom: none;
}
#app > article .router-content {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      overflow-y: auto;
}
#app > article .router-content > .content {
